/* Blocks */
.parallax {
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  opacity: 0.18;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
}
.button {
  padding: 15px 20px;
  border-radius: 6px;
  text-align: center;
  transition: 0.3s;
  border: 1px solid black;
  cursor: pointer;
}
.button--dark,
.button--light:hover {
  background-color: black;
  color: white;
  fill: white;
}
.button--light,
.button--dark:hover {
  background-color: white;
  color: black;
  fill: black;
}
.button--small {
  width: 15rem;
}
.choices {
  width: 200px;
  margin: 0;
  border-radius: 20px;
  flex-shrink: 0;
}
.choices__inner {
  min-height: unset;
  width: 200px;
  font-size: 12px;
  padding: 0 !important;
  box-sizing: border-box;
  border: 1px solid black !important;
  border-radius: 20px;
  color: black;
  background-color: white;
  cursor: pointer;
}
.choices__item {
  padding: 10px !important;
}
.choices__list {
  padding: 0;
}
.choices__item--disabled {
  display: none;
}
.nav-menu__list {
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 25px;
}
.nav-menu__list__item {
  list-style: none;
  text-align: center;
}
.nav-menu-button {
  display: none;
  padding: 0;
  border: 0;
  width: 40px;
  height: 40px;
  background-color: white;
  margin-right: 10px;
}
.nav-menu-button svg {
  width: 40px;
  height: 40px;
}
.nav-menu-button.active {
  background-color: black;
  color: white;
}
.nav-menu-button.active svg {
  fill: white;
}
@media screen and (max-width: 1024px) {
  .nav-menu {
    visibility: hidden;
    position: fixed;
    top: 80px;
    right: 0;
    left: 0;
    padding: 20px;
    background-color: white;
    z-index: 5;
  }
  .nav-menu-button {
    display: block;
  }
  .nav-menu__list {
    height: auto;
    flex-direction: column;
  }
}
@media screen and (max-width: 560px) {
  .nav-menu {
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.header {
  width: 100%;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  margin: 10px;
}
.header__contacts {
  margin-right: 10px;
}
@media screen and (max-width: 1024px) {
  .header {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 10;
  }
}
.logo {
  width: 205px;
  height: 60px;
}
.swiper {
  margin: 0;
}
.swiper-pagination-bullet-active {
