}
.contacts__phone {
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  margin: 10px;
}
.contacts__e-mail {
  color: inherit;
}
.promo-slider {
  width: 100%;
  height: calc(100vh - 80px);
}
.promo-slider__item {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  align-items: flex-end;
  height: 100%;
}
.promo-slider__item__text {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 15px 50px;
