.faq {
  background-color: var(--main-bg-color-dark);
  padding: var(--padding-y-defult) var(--padding-x-defult);
}
.faq__title {
  font-size: 54px;
  color: white;
  margin-bottom: 30px;
  text-transform: uppercase;
}
.faq__lists-wraper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 1em;
}
.faq__list {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 50%;
  max-width: 750px;
}
.faq__list__item {
  list-style: none;
}
.faq__list__item ul {
  padding-left: 1em;
}
.faq__list__item > details {
  padding: 0 1em;
  position: relative;
  border-radius: 6px;
  background-color: #ECEFF1;
  color: #263238;
  transition: all 1.35s ease-in-out;
  overflow: hidden;
}
.faq__list__item > details[open] {
  background-color: #FFF;
}
.faq__list__item > details[open]::before {
  opacity: 0.6;
}
.faq__list__item__question {
  padding: 1em 2em 1em 0;
  display: block;
  position: relative;
  font-size: 1.33em;
  font-weight: bold;
  cursor: pointer;
}
.faq__list__item__question::before,
.faq__list__item__question::after {
  width: 0.75em;
  height: 2px;
  position: absolute;
  top: 50%;
  right: 0;
  content: '';
  background-color: currentColor;
  text-align: right;
  transform: translateY(-50%);
  transition: transform 0.2s ease-in-out;
}
.faq__list__item__question::after {
  transform: translateY(-50%) rotate(90deg);
}
.faq__list__item > details[open] .faq__list__item__question::after {
  transform: translateY(-50%) rotate(180deg);
}
.faq__list__item__question::-webkit-details-marker {
  display: none;
}
.faq__list__item__answer {
  padding-bottom: 1em;
}
@media screen and (max-width: 768px) {
  .faq__lists-wraper {
    flex-direction: column;
  }
  .faq__list {
    width: 100%;
  }
}
@media screen and (max-width: 550px) {
  .faq__title {
    font-size: 40px;
  }
}
.footer {
  background-color: var(--main-bg-color-dark);
